@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans&family=Roboto&display=swap);
:root {
  --page-width: 100rem;
  --text-font-family: 'Roboto', sans-serif;
  --header-font-family: 'Merriweather Sans', sans-serif;
  --text-font-size: 1.1rem;
  --color-pharo-blue: #3297d4;
  --color-pharo-light-blue: #dcedf7;
  --color-dark-gray: #616161;
  --color-light-gray: #f5f5f5;
}

body {
  font-family: 'Roboto', sans-serif;
  font-family: var(--text-font-family);
  font-size: 1.1rem;
  font-size: var(--text-font-size);
  position: absolute;
  margin: 0;
  width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Merriweather Sans', sans-serif;
  font-family: var(--header-font-family);
  color: #3297d4;
  color: var(--color-pharo-blue);
  margin-top: 30px;
  margin-bottom: 20px;
}

h1 {
  font-size: 40px;
  margin-bottom: 50px;
  text-align: center;
}

.container {
  max-width: 100rem;
  max-width: var(--page-width);
  margin-left: auto;
  margin-right: auto;
  padding: 50px 0;
}

li, p {
  letter-spacing: .010em;
  line-height: 1.6em;
}

li {
  margin-bottom: 0.4rem;
}

a {
  color: #3297d4;
  color: var(--color-pharo-blue);
}

#navbar {
  display: flex;
  overflow: visible;
  border-bottom: 5px solid var(--color-light-gray);
  /* margin-bottom: 20px; */
}

#navbar img {
  display: inline;
  height: 33px;
  vertical-align: middle;
  padding-right: 17px;
}

#navbar-buttons {
  /* max-width: var(--page-width); */
  margin: 0 auto;
  text-align: center;
  font-family: var(--text-font-family);
  font-size: var(--text-font-size);
}

#navbar-buttons ul {
  margin: 0;
}

#navbar li {
  display: inline;
  font-size: 15px;
  text-decoration: none;
}

a.navbar-button {
  display: inline-block;
  padding: 15px 14px;
  color: var(--color-pharo-blue);
  text-decoration: none;
}

a:hover.navbar-button {
  background-color: var(--color-pharo-light-blue);
}

a:active.navbar-button {
  background-color: var(--color-pharo-blue);
  color: white;
}

form {
  border-bottom: 1px solid var(--color-light-gray);
  padding-bottom: 20px;
  margin-bottom: 30px;
}

.ideas-container {
  max-width: 1100px;
}

.ideas {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  grid-auto-rows: auto;
  grid-gap: 1rem;
}

aside {
  float: left;
  width: 23%;
  overflow: hidden;
  /* border-right: 3px solid var(--color-light-gray); */
}

article {
  float: right;
  width: 75%;
}


.modal {
  background-color: #fefefe;
  margin: 1% auto;
  border-radius: 5px;
  border: 1px transparent;
  width: 93%; 
}

.header-modal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.title-modal {
  max-width: 70%;
}

.img-modal {
  width: 30%;
  object-fit: cover;
}

.MuiDialog-paper {
  margin: 12px !important;
  position: relative;
  overflow-y: auto;
}

.ideasGrid {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.ideaCard {
  background: #f7f7fa;
  border: 1px solid #e3e2ef;
  box-sizing: border-box;
  box-shadow: 0 4px 10px -4px rgba(70, 70, 130, 0.3);
  border-radius: 8px;
  padding: 0.8rem;
  flex-grow: 1;
  flex-basis: 0;
  margin: 1%;
  transition: all 0.3s ease-in-out;
  position: relative;
}
.ideaCard:hover {
  box-shadow: 0 10px 20px -10px rgba(70, 70, 130, 0.5);
}
.ideaCard:nth-child(1),
.ideaCard:nth-child(2) {
  flex-grow: 2;
  flex-basis: 48%;
}

.ideaCard:nth-child(3),
.ideaCard:nth-child(4),
.ideaCard:nth-child(5) {
  flex-grow: 2;
  flex-basis: 30%;
}
.header {
  background: #ffffff;
  border: 1px solid #e3e2ef;
  box-sizing: border-box;
  border-radius: 8px;
  padding: 1.6rem;
  min-height: 18rem;
  display: flex;
  flex-direction: column;
}
.header a {
  font-size: 2rem;
  font-weight: 600;
}
.projectLogo img {
  width: 100%;
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  border-radius: 8px;
}
.projectDescription {
  padding: 0.8rem 1.6rem 1.6rem;
}

@media screen and (max-width: 800px) {
  .ideaCard {
    flex-grow: 1;
    flex-basis: 100%;
    margin: 0 -1.6rem;
    margin-bottom: 0.8rem;
  }
  .ideaCard:nth-child(n) {
    flex-grow: 1;
    flex-basis: 100%;
  }
  .projectDescription {
    padding: 0 0.8rem;
    font-size: 1.4rem;
  }
  .projectLogo {
    margin-bottom: 0rem;
  }
  .header {
    text-align: center;
  }
}

